import { CharState } from "./CharState";
import KeyButton from "./KeyButton";

export type KeyCallbacks = {
  onKey: (keyChar: string) => void;
  onBackspace: () => void;
  onReturn: () => void;
};

export function CreateKey(
  keyChar: string,
  keyState: CharState,
  callbacks: KeyCallbacks
) {
  switch (keyChar) {
    case "return":
      return (
        <KeyButton
          keyChar=""
          isWide={true}
          buttonProps={{ variant: "secondary", onClick: callbacks.onReturn }}
        />
      );
    case "backspace":
      return (
        <KeyButton
          keyChar=""
          isWide={true}
          buttonProps={{ variant: "secondary", onClick: callbacks.onBackspace }}
        />
      );
    case null:
      return (
        <KeyButton
          keyChar=""
          isWide={false}
          buttonProps={{ variant: "secondary" }}
        />
      );
    default:
      var variant;
      switch (keyState) {
        case CharState.Incorrect:
          variant = "secondary";
          break;
        case CharState.Correct:
          variant = "success";
          break;
        case CharState.SemiCorrect:
          variant = "warning";
          break;
        case CharState.Default:
        default:
          variant = "primary";
          break;
      }

      return (
        <KeyButton
          keyChar={keyChar.toUpperCase()}
          isWide={false}
          buttonProps={{
            onClick: () => callbacks.onKey(keyChar),
            variant: variant,
          }}
        />
      );
  }
}
