import { CharState } from "./CharState";
import "./Keyboard.scss";
import { CreateKey, KeyCallbacks } from "./KeyFactory";

type KeyboardProps = {
  keyboardLayout: string[][];
  keyCallbacks: KeyCallbacks;
  keyCharStates: {[keyChar: string]: CharState};
};

function Keyboard(props: KeyboardProps) {
  return (
    <div className="Keyboard">
      {props.keyboardLayout.map((row) => (
        <div key={row.toString()}>
          {row.map((keyChar) => CreateKey(keyChar, props.keyCharStates[keyChar] || CharState.Default, props.keyCallbacks))}
        </div>
      ))}
    </div>
  );
}

export default Keyboard;
