import isMobile from "is-mobile";
import { Icon } from "react-web-share";
import { CharState } from "./CharState";
import "./ShareButton.scss";
import FormatTime from "./TimeFormatter";

export type ShareButtonProps = {
  hours: number;
  minutes: number;
  seconds: number;
  today: Date;
  clues: [clueChar: string, charState: CharState][][];
};

function formatClues(
  clues: [clueChar: string, charState: CharState][][]
): string {
  return clues
    .map((clue) =>
      clue
        .map(([_, charState]) => {
          switch (charState) {
            case CharState.Correct:
              return "";
            case CharState.SemiCorrect:
              return "";
            default:
              return "";
          }
        })
        .join("")
    )
    .join("\n");
}

export function ShareButton(props: ShareButtonProps) {
  const isMobileOrTablet = isMobile({ tablet: true }) && window.navigator.share;
  const text =
    `${props.today.toLocaleDateString()}\n` +
    "I solved today's Speedle!\n\n" +
    `Time: ${FormatTime(props.hours, props.minutes, props.seconds)}\n` +
    formatClues(props.clues) +
    "\n\n\n";
  const url = "https://frugs.github.io/speedle/";
  const title = "Share your time!";
  const data = {
    text: isMobileOrTablet ? text : encodeURIComponent(text),
    url: url,
    title: title,
  };
  const sites = [
    "whatsapp",
    "twitter",
    "telegram",
    "facebook",
    "vk",
    "okru",
    "mail",
    "copy",
  ];

  return (
    <div className="IconList">
      {sites.map((name) => (
        <Icon name={name} data={data} />
      ))}
    </div>
  );
}
