import "./GuessOrClue.scss";
import { Card } from "react-bootstrap";

export type GuessProps = {
  guess: string[];
  isCorrect: boolean;
};

export function Guess(props: GuessProps) {
  return (
    <div className="GuessOrClue">
      {Array.from(Array(5).keys()).map((index) => {
        const char = props.guess[index] || "";
        return (
          <Card
            bg={props.isCorrect ? "success" : "secondary"}
            key={`${char} ${index}`}
            className="GuessOrClueChar"
          >
            {char.toUpperCase()}
          </Card>
        );
      })}
    </div>
  );
}
