export enum CharState {
  Default = 0,
  Incorrect,
  SemiCorrect,
  Correct,
}

export function GetCharState(
  clue: string[],
  index: number,
  answer: string[]
): CharState {
  const indexOf = answer.indexOf(clue[index]);
  if (indexOf === -1) {
    return CharState.Incorrect;
  }

  if (clue[index] === answer[index]) {
    return CharState.Correct;
  }

  let semiCorrectCount = 0;
  for (let i = 0; i < answer.length; i++) {
    if (answer[i] === clue[index] && answer[i] !== clue[i]) {
      semiCorrectCount++;
    }
  }

  for (let i = 0; i < index; i++) {
    if (clue[i] === clue[index] && answer[i] !== clue[i]) {
      semiCorrectCount--;
    }
  }

  if (semiCorrectCount > 0) {
    return CharState.SemiCorrect;
  }

  return CharState.Incorrect;
}

export function GetCharStateFromClues(
  answer: string[],
  clues: string[][]
): { [char: string]: CharState } {
  const result = {} as { [char: string]: CharState };
  clues.forEach((clue) =>
    clue.forEach((clueChar, index) => {
      const charState = GetCharState(clue, index, answer);
      const prevState = result[clueChar] || CharState.Default;
      if (charState > prevState) {
        result[clueChar] = charState;
      }
    })
  );
  return result;
}
