import { Button, Modal } from "react-bootstrap";
import "./CorrectGuessModal.scss";
import { Guess, GuessProps } from "./Guess";
import { ShareButton, ShareButtonProps } from "./ShareButton";
import FormatTime from "./TimeFormatter";

type CorrectGuessModalProps = ShareButtonProps & {
  hours: number;
  minutes: number;
  seconds: number;
  countDownHours: number;
  countDownMinutes: number;
  countDownSeconds: number;
  modalProps?: any;
  guessProps: GuessProps;
};

function CorrectGuessModal(props: CorrectGuessModalProps) {
  return (
    <Modal centered {...props.modalProps}>
      <Modal.Header className="CorrectGuessModalHeader">
        <Modal.Title>Congratulations!</Modal.Title>
      </Modal.Header>
      <Modal.Body className="CorrectGuessModalBody">
        <div style={{ margin: "1em" }}>
          <Guess {...props.guessProps} />
        </div>
        <div style={{ margin: "1em" }}>
          Your guess was correct! Your time was:
        </div>
        <div className="CorrectGuessModalTime">
          {FormatTime(props.hours, props.minutes, props.seconds)}
        </div>
        <div className="NextPuzzleIndicator">
          {props.countDownHours +
            props.countDownMinutes +
            props.countDownSeconds >
          0 ? (
            <div>
              Next puzzle available in{" "}
              {FormatTime(
                props.countDownHours,
                props.countDownMinutes,
                props.countDownSeconds
              )}
            </div>
          ) : (
            <Button as="a" variant="link" onClick={window.location.reload}>
              Refresh your browser for the next puzzle!
            </Button>
          )}
        </div>
      </Modal.Body>
      <Modal.Footer className="CorrectGuessModalFooter">
        <ShareButton {...props} />
      </Modal.Footer>
    </Modal>
  );
}

export default CorrectGuessModal;
