import _ from "lodash";
import { create } from "random-seed";
import { useCallback, useEffect, useState } from "react";
import { Container } from "react-bootstrap";
import { useStopwatch, useTimer } from "react-timer-hook";
import IsAlphabetic from "./Alphabet";
import "./App.scss";
import { CharState, GetCharState, GetCharStateFromClues } from "./CharState";
import Clues from "./Clues";
import CorrectGuessModal from "./CorrectGuessModal";
import GenerateClues from "./GenerateClues";
import { Guess } from "./Guess";
import Keyboard from "./Keyboard";
import QWERTY from "./KeyboardLayouts";
import { LoadLocalData, SaveLocalData } from "./LocalData";
import StopWatch from "./StopWatch";
import { GetAnswer, IsGuessAllowed } from "./WordList";

function App() {
  const today = new Date();
  const todaySeed = today.toLocaleDateString("en-ca");
  const rng = create(todaySeed);

  const [localData, setLocalData] = useState(() => LoadLocalData(todaySeed));
  useEffect(() => SaveLocalData(localData), [localData]);

  const [answer] = useState(() => GetAnswer(rng.random));
  const [guess, setGuess] = useState(
    localData.isCorrectGuess ? answer : ([] as string[])
  );
  const [isCorrectGuess, setCorrectGuess] = useState(
    !!localData.isCorrectGuess
  );
  const [clues] = useState(() => GenerateClues(answer, rng.random));
  const cluesWithCharStates = clues.map((clue) =>
    clue.map((clueChar, index): [string, CharState] => [
      clueChar,
      GetCharState(clue, index, answer),
    ])
  );

  const { days, hours, minutes, seconds, pause } = useStopwatch({
    autoStart: !isCorrectGuess,
    offsetTimestamp: (() => {
      const offset = new Date();
      offset.setSeconds(offset.getSeconds() + (localData.elapsedSeconds || 0));
      return offset;
    })(),
  });

  const countDownExpiryTimestamp = new Date();
  countDownExpiryTimestamp.setHours(23, 59, 59, 999);
  const {
    hours: countDownHours,
    minutes: countDownMinutes,
    seconds: countDownSeconds,
  } = useTimer({ autoStart: true, expiryTimestamp: countDownExpiryTimestamp });

  useEffect(() => {
    setLocalData((prevState) => ({
      ...prevState,
      ...{
        elapsedSeconds:
          seconds + minutes * 60 + hours * 60 * 60 + days * 60 * 60 * 24,
      },
    }));
  }, [days, hours, minutes, seconds, setLocalData]);

  const onKey = (keyChar: string) => {
    setGuess((prevState) => {
      if (prevState.length === 5) {
        return prevState;
      }
      return [...prevState, keyChar];
    });
  };
  const onBackspace = useCallback(() => {
    if (isCorrectGuess) {
      return;
    }

    setGuess((prevState) => prevState.slice(0, -1));
  }, [isCorrectGuess]);

  const onReturn = useCallback(() => {
    if (guess.length !== 5 || !IsGuessAllowed(guess)) {
      return;
    }

    if (_.isEqual(guess, answer)) {
      setCorrectGuess(true);
      setLocalData((prevState) => ({
        ...prevState,
        ...{ isCorrectGuess: true },
      }));
      pause();
    } else {
      setGuess([]);
    }
  }, [guess, answer, pause]);

  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      switch (event.key) {
        case "Enter":
          onReturn();
          break;
        case "Backspace":
          onBackspace();
          break;
        default:
          if (IsAlphabetic(event.key)) {
            onKey(event.key.toLowerCase());
          }
          break;
      }
    };
    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, [onBackspace, onReturn]);

  return (
    <Container className="App" fluid="md">
      <CorrectGuessModal
        hours={hours}
        minutes={minutes}
        seconds={seconds}
        today={today}
        countDownHours={countDownHours}
        countDownMinutes={countDownMinutes}
        countDownSeconds={countDownSeconds}
        clues={cluesWithCharStates}
        modalProps={{ show: isCorrectGuess }}
        guessProps={{ isCorrect: true, guess: answer }}
      />
      <StopWatch hours={hours} minutes={minutes} seconds={seconds} />
      <Clues clues={cluesWithCharStates} answer={answer} />
      <Guess guess={guess} isCorrect={isCorrectGuess} />
      <Keyboard
        keyboardLayout={QWERTY}
        keyCallbacks={{
          onKey: onKey,
          onBackspace: onBackspace,
          onReturn: onReturn,
        }}
        keyCharStates={GetCharStateFromClues(answer, clues)}
      />
    </Container>
  );
}

export default App;
