import "./KeyButton.scss";
import { Button } from "react-bootstrap";

type KeyButtonProps = {
  keyChar: string;
  isWide: boolean;
  buttonProps: any;
};

function KeyButton(props: KeyButtonProps) {
  return (
    <Button
      key={props.keyChar}
      className={`${props.isWide ? "WideKeyButton" : ""} KeyButton`}
      {...props.buttonProps}
    >
      {props.keyChar.toUpperCase()}
    </Button>
  );
}

export default KeyButton;
