import "./Clues.css"
import "./GuessOrClue.scss";
import { Card } from "react-bootstrap";
import { CharState } from "./CharState";

type CluesProps = {
  clues: [clueChar: string, charState: CharState][][];
  answer: string[];
};

function Clues(props: CluesProps) {
  return (
    <div className="Clues">
      {props.clues.map((clue) => {
        return (
          <div key={clue.toString()} className="GuessOrClue">
            {clue.map(([clueChar, charState], index) => {
              var bg;
              switch (charState) {
                case CharState.Correct:
                  bg = "success";
                  break;
                case CharState.SemiCorrect:
                  bg = "warning";
                  break;
                case CharState.Incorrect:
                case CharState.Default:
                default:
                  bg = "secondary";
                  break;
              }
              return (
                <Card
                  className="GuessOrClueChar"
                  bg={bg}
                  key={`${clueChar} ${index}`}
                >
                  {clueChar.toUpperCase()}
                </Card>
              );
            })}
          </div>
        );
      })}
    </div>
  );
}

export default Clues;
