import "./StopWatch.scss";
import FormatTime from "./TimeFormatter";

type StopWatchProps = {
  hours: number;
  minutes: number;
  seconds: number;
};

function StopWatch(props: StopWatchProps) {
  return (
    <div className="StopWatch">
      {FormatTime(props.hours, props.minutes, props.seconds)}
    </div>
  );
}

export default StopWatch;
