type LocalData = {
  elapsedSeconds?: number;
  todaySeed?: string;
  isCorrectGuess?: boolean;
};

export function LoadLocalData(todaySeed: string): LocalData {
  const parsed = JSON.parse(localStorage.getItem("local_data") || "{}");
  return parsed.todaySeed === null ||
    parsed.todaySeed === undefined ||
    parsed.todaySeed !== todaySeed
    ? { todaySeed: todaySeed }
    : parsed;
}

export function SaveLocalData(localData: LocalData) {
  localStorage.setItem("local_data", JSON.stringify(localData));
}
