import _ from "lodash";
import { CharState, GetCharState } from "./CharState";
import { GetAnswer, Answers } from "./WordList";

function isClueAllowed(
  clue: string[],
  answer: string[],
  previousClue: string[]
): boolean {
  if (_.isEqual(clue, answer)) {
    return false;
  }

  if (!previousClue) {
    return true;
  }

  const previousClueCharStates = previousClue.reduce(
    (a, b) => ({ ...a, [b]: [] }),
    {} as { [clueChar: string]: CharState[] }
  );
  previousClue.forEach((clueChar, index) => {
    const charState = GetCharState(previousClue, index, answer);
    if (charState >= CharState.SemiCorrect) {
      previousClueCharStates[clueChar].push(charState);
    }
  });

  var correctCount = 0;
  var semiCorrectCount = 0;
  for (let i = 0; i < previousClue.length; i++) {
    const previousClueCharState = GetCharState(previousClue, i, answer);
    const clueCharState = GetCharState(clue, i, answer);

    if (
      previousClueCharState === CharState.Correct &&
      clueCharState < previousClueCharState
    ) {
      return false;
    }

    if (clueCharState === CharState.Correct) {
      correctCount++;
    }

    if (clueCharState === CharState.SemiCorrect) {
      semiCorrectCount++;
    }

    const prevCharStatesForClueChar = previousClueCharStates[clue[i]];
    if (prevCharStatesForClueChar) {
      const indexOf = prevCharStatesForClueChar.indexOf(clueCharState);
      if (indexOf !== -1) {
        prevCharStatesForClueChar.splice(indexOf, 1);
      }
    }
  }

  if (
    Object.values(previousClueCharStates).reduce((a, b) => a.concat(b))
      .length !== 0
  ) {
    return false;
  }

  if (correctCount > 2) {
    return false;
  }

  if (correctCount + semiCorrectCount > 3) {
    return false;
  }

  return true;
}

function GenerateClues(answer: string[], rng: () => number): string[][] {
  const clues = [] as string[][];
  while (clues.length < 5) {
    if (clues.length === 0) {
      clues.push(GetAnswer(rng));
      continue;
    }

    const potentialClues = Answers.filter(
      (potentialClue) =>
        potentialClue !== answer &&
        !clues.some((elem) => _.isEqual(elem, potentialClue)) &&
        isClueAllowed(potentialClue, answer, clues[clues.length - 1])
    );

    if (potentialClues.length === 0) {
      clues.splice(0, clues.length);
      continue;
    }

    clues.push(potentialClues[Math.floor(rng() * potentialClues.length)]);
  }
  return clues;
}

export default GenerateClues;
